<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5.overflow-hidden
    .titulo-principal.color-acento-contenido
      .titulo-principal__numero
        span 1
      h1 Inventarios


    .row.align-items-stretch.mb-5
      .col-lg-7.order-2.order-lg-1
        .bg-color-2.p-4.h-100
          p.mt-xl-4 Antes de hablar del registro de compras y ventas, es importante enfocarse en el tema de los inventarios para la venta, produccin o prestacin de servicios, toda vez que constituyen la fuente generadora de ingresos para la empresa y por tanto requieren de un tratamiento especial.

      .col-lg-5.order-1.order-lg-2
        figure
          img(src="@/assets/curso/tema1/1.png", alt="").mb-4.mb-lg-0

    #t_1_1.titulo-segundo.color-acento-contenido.mb-5(data-aos="flip-up")
      h2 1.1  Poltica contable

    p.mb-4 Dentro del manual de polticas contables establecido por la empresa, es de especial importancia el captulo correspondiente a los inventarios, pues a travs de este se establecen los lineamientos a seguir para la determinacin del sistema y mtodo de valuacin de los inventarios y su correspondiente tratamiento en las diferentes operaciones.

    .row.mb-5
      .col-lg-4
        figure
          img(src="@/assets/curso/tema1/2.svg", alt="", style="max-width: 400px").mx-auto.mb-4

      .col-lg-8
        p.mb-4 De igual forma, la poltica contable de inventarios debe establecer los gastos correlacionados en una venta, donde lo principal corresponde al costo de venta, pues permite determinar el resultado de la operacin.

        .bg-color-3.p-4.mb-4
          .row.align-items-center
            .col-lg-auto
              img(src="@/assets/curso/tema1/3.svg", style="max-width: 90px").mx-auto
            .col-lg
              p.mb-0 Otro aspecto fundamental que debe incluirse en la poltica de inventarios es el establecimiento de los precios de venta, pues la seccin 13 de NIIF para Pymes (2009) requiere que se haga una comparacin entre el costo de adquisicin y los egresos necesarios para ponerlo en condiciones de venta y el precio de venta neto estimado, para determinar los costos de venta del inventario.

        .row.align-items-center
          .col-lg-auto.d-none.d-lg-block
            img(src="@/assets/curso/tema1/4.svg", style="max-width: 90px").mx-auto
          .col-lg
            p.mb-0 Adems de las compras, ventas de inventarios y las devoluciones que de estas se desprenden, tambin hacen parte de las polticas contables, los traslados entre bodegas y los procesos para el tratamiento de deterioros, y bajas de inventarios.


    .row.mb-5
      .col-lg-7
        
        p.mb-4 En la poltica contable de inventarios se debe establecer el sistema de inventarios a utilizar, el cual puede ser:

        .tarjeta.bg-color-4.p-4
          ul.lista-ul--color
            li.mb-3.d-flex.mb-2
              i.fas.fa-chevron-right
              p.mb-0 Una NIIF se aplica especficamente a una transaccin y las polticas contables aplicadas a esa partida se determinarn con dicha NIIF.
            li.mb-3.d-flex.mb-2 
              i.fas.fa-chevron-right
              p.mb-0 En las NIIF se establecen polticas contables sobre las que el IASB (#[em International Accounting Standards Board]) Junta de Normas Internacionales de Contabilidad ha llegado a la conclusin de que dan lugar a estados financieros con informacin relevante y fiable sobre las transacciones.
            li.mb-3.d-flex.mb-2
              i.fas.fa-chevron-right
              p.mb-0 Las NIIF se acompaan de guas que ayudan a las entidades a aplicar sus requerimientos.
            li.mb-3.d-flex.mb-2
              i.fas.fa-chevron-right
              p.mb-0 En ausencia de una NIIF que sea aplicable especficamente a una transaccin, la gerencia deber usar su juicio en el desarrollo y aplicacin de una poltica contable, a fin de suministrar informacin que sea: relevante, fiable, fidedigna, neutral, prudente y que reflejen la esencia econmica de las transacciones y no simplemente su forma legal.
            li.mb-3.d-flex
              i.fas.fa-chevron-right
              p.mb-0 La gerencia se referir a las fuentes para considerar su aplicabilidad; como los requerimientos de temas relacionados y las definiciones, reconocimiento y medicin para los elementos de los estados financieros.

      .col-lg-5.d-none.d-lg-block
        figure
          img(src="@/assets/curso/tema1/5.png", alt="").mb-4.mb-lg-0

    p.mb-5 Por otro lado, es importante tener en cuenta los siguientes aspectos contemplados por la NIC 2 para el correspondiente diseo de la poltica contable de los inventarios:

    .bg-full-width.bg-fondo-1
      .px-4.px-md-5.pb-5

        .carousel-bg

          .row.justify-content-center.align-items-center.my-5

            .col-lg-3.d-none.d-lg-block(data-aos="zoom-in")
              img(src='@/assets/curso/tema1/6.png', style="max-width: 100%", alt='').m-auto

            .col-lg-8

              SlyderF.mb-5(columnas="col-lg-6")(data-aos="zoom-in-up")
                .tarjeta.bg-color-white.p-4.h-100.shadow-app.text-center
                  img(src='@/assets/curso/tema1/icono-1.svg' alt='AvatarTop' style="max-width: 90px").mx-auto.mb-3
                  p.text-center Deben ser considerados como activos posedos para ser vendidos en el curso normal de la operacin.
                .tarjeta.bg-color-white.p-4.h-100.shadow-app.text-center
                  img(src='@/assets/curso/tema1/icono-2.svg' alt='AvatarTop' style="max-width: 90px").mx-auto.mb-3
                  p.text-center Identificados como elementos del proceso de produccin con destino a ser vendidos.
                .tarjeta.bg-color-white.p-4.h-100.shadow-app.text-center
                  img(src='@/assets/curso/tema1/icono-3.svg' alt='AvatarTop' style="max-width: 90px").mx-auto.mb-3
                  p.text-center Establecidos como materiales o suministros que sern consumidos en el proceso de produccin o en la prestacin de servicios.
                .tarjeta.bg-color-white.p-4.h-100.shadow-app.text-center
                  img(src='@/assets/curso/tema1/icono-4.svg' alt='AvatarTop' style="max-width: 90px").mx-auto.mb-3
                  p.text-center Valor neto realizable es el precio estimado de venta de un activo en el curso normal de la operacin menos los costos estimados para terminar su produccin y los necesarios para llevar a cabo la venta.
                .tarjeta.bg-color-white.p-4.h-100.shadow-app.text-center
                  img(src='@/assets/curso/tema1/icono-5.svg' alt='AvatarTop' style="max-width: 90px").mx-auto.mb-3
                  p.text-center Valor razonable es el precio que se recibira por vender un activo o que se pagara por transferir un pasivo en una transaccin ordenada entre participantes del mercado en la fecha de la medicin.
                .tarjeta.bg-color-white.p-4.h-100.shadow-app.text-center
                  img(src='@/assets/curso/tema1/icono-6.svg' alt='AvatarTop' style="max-width: 90px").mx-auto.mb-3
                  p.text-center Entre los inventarios tambin se incluyen los bienes comprados y almacenados para su reventa, entre los que se encuentran, por ejemplo, las mercaderas adquiridas por un minorista para la reventa a sus clientes, y tambin los terrenos u otras propiedades de inversin que se tienen para ser vendidos a terceros.
                .tarjeta.bg-color-white.p-4.h-100.shadow-app.text-center
                  img(src='@/assets/curso/tema1/icono-7.svg' alt='AvatarTop' style="max-width: 90px").mx-auto.mb-3
                  p.text-center Tambin son inventarios los productos terminados o en curso de fabricacin mantenidos por la entidad, as como los materiales y suministros para ser usados en el proceso productivo.


