    #t_1_2.titulo-segundo.color-acento-contenido.mb-5(data-aos="flip-up")
      h2 1.2  Concepto, tipos de inventario, valuacin de inventarios

    p.mb-5 De conformidad con la NIC 2, se definen los diferentes tipos de costos, de la siguiente manera:


    .bg-full-width.bg-fondo-slider.mb-5
      .p-4.p-md-5
        SlyderA(tipo="b").bg-white.p-4.tarjeta
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.order-2
              figure
                img(src="@/assets/curso/tema1/slider-1.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              p Los inventarios se medirn al costo o al valor neto realizable, segn cul sea menor.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.order-2
              figure
                img(src="@/assets/curso/tema1/slider-2.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              p El costo de los inventarios comprender todos los costos derivados de su adquisicin, transformacin, as como otros costos en los que se haya incurrido para darles su condicin y ubicacin actuales.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.order-2
              figure
                img(src="@/assets/curso/tema1/slider-3.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              p El costo de adquisicin de los inventarios comprender el precio de compra, los aranceles de importacin y otros impuestos (que no sean recuperables posteriormente de las autoridades fiscales) y transporte, manejo y otros costos directamente atribuibles a la adquisicin de mercaderas, materiales y servicios. Los descuentos comerciales, las rebajas y otras partidas similares se deducirn para determinar el costo de adquisicin.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.order-2
              figure
                img(src="@/assets/curso/tema1/slider-4.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              p Los costos de transformacin de los inventarios comprendern aquellos costos relacionados con las unidades de produccin, tales como la materia prima directa, la mano de obra directa y los costos indirectos de produccin.
          .row.align-items-center.p-4.p-md-5
            .col-lg-5.order-2
              figure
                img(src="@/assets/curso/tema1/slider-5.png")
            .col-lg-7.order-1.mb-3.mb-lg-0
              p Son costos indirectos fijos de produccin los que permanecen relativamente constantes, con independencia del volumen de produccin, tales como la depreciacin y mantenimiento de los edificios y equipos de la fbrica y los activos por derecho de uso utilizados en el proceso de produccin.


    p.mb-4.d-none.d-lg-block De acuerdo con la seccin 13 de NIIF para Pymes (2009), los inventarios son activos:

