                      p.mb-0 En la prestacin de un servicio.



    p.mb-4 La necesidad de controlar los inventarios requiere la aplicacin de un sistema que permita determinar el costo de existencia y el costo de venta; es as como se debe elegir entre dos opciones, el sistema peridico y el sistema permanente.

    .tarjeta--container.row.mb-5
      .col-md.tarjeta.bg-color-5.text-white.p-5
        figure.mb-4
          img(src='@/assets/curso/tema1/8.svg', alt='', style="max-width: 85px")
          
        p.text-white.mb-4 El sistema de inventarios peridico tiene las siguientes caractersticas:

        ol.lista-ol--cuadro
          li 
            .lista-ol--cuadro__vineta
              span a
            | Se hace conteo fsico para hallar el costo de existencia.
          li 
            .lista-ol--cuadro__vineta
              span b
            | Solo al cierre del ejercicio se halla el costo de venta.
          li 
            .lista-ol--cuadro__vineta
              span c
            | Solo hay un mtodo de valuacin "Juego de Inventarios".
          li 
            .lista-ol--cuadro__vineta
              span d
            | El krdex solo mueve unidades sin costos.

      .col-md.tarjeta.bg-color-6.p-5

        figure.mb-4
          img(src='@/assets/curso/tema1/9.svg', alt='', style="max-width: 85px")
          
        p.mb-4 El sistema de inventarios permanente tiene las siguientes caractersticas:

        ol.lista-ol--cuadro
          li 
            .lista-ol--cuadro__vineta
              span a
            | Se hace conteo fsico por control y no para hallar costos.
          li 
            .lista-ol--cuadro__vineta
              span b
            | Costos de venta y existencia se hallan en cualquier momento.
          li 
            .lista-ol--cuadro__vineta
              span c
            | Se debe elegir uno entre varios mtodos de valuacin "Promedio ponderado, Primeras en Entrar Primeras en Salir".
          li 
            .lista-ol--cuadro__vineta
              span d
            | El krdex mueve tanto unidades como costos.


    p.mb-4 A continuacin, se presenta un ejercicio utilizando el mtodo de valuacin por Promedio Ponderado, el cual emplea la media aritmtica para determinar el costo unitario, de la siguiente manera:

